@import  "../../../../theme/variables.scss";
@import  "../../../../theme/responsive-styles.scss";

.page-header{
    width:100%;
    height:100px;
    background: linear-gradient(180deg, #FCA364, #FD7D4F);
    @media only screen  and (min-width : map-get($grid-breakpoints, lg) ) {
        height:180px;
      }
    ion-tab-bar{
        position: relative;
        width: 315px;
        height: 38px;
        border-radius: 25px;
        --background: #F4F5F8;
        --color: #000000;
        padding: 0px 5px 0px 5px;
        margin:auto;
        margin-top: 12px;
        border:0.5px solid #FBA464;
        @media only screen  and (min-width :map-get($grid-breakpoints, xs)) {
            width: 275px;
          }
    }
    ion-tab-button,a{
        border-radius: 25px;
    }

    .tab-selected{
        --background: white;
        border-radius: 20px;
        height: 30px;
        color: #000000;
        box-shadow: 0px 2px 5px #00000038;
        margin-right:1px;
    }
    .tab-selected:active{
        --background: #ffd8bc;
    }
  
}
#header-icon{
    float: right;
    padding-top: 3.7vh;
    padding-right: 2.67vw;
}